.App {
  text-align: center;
}

.tooltip {
  position: absolute;
  z-index: 10;
  opacity: 1!important;			
  width: auto;
  max-width: 50vw;			
  height: auto;					
  padding: 10px;				
  font: 12px sans-serif;		
  background: white;
  border: 1px solid #999;		
  border-radius: 8px;			
  pointer-events: none;	
  font-size: .7em!important;		
}

.toolTipTextStyle {
  display: flex; 
  align-items:center
}

.toolTipFont {
  color: #0077b6;
}

.legendText {
  font-size: 12px;
}

.barChartLegendRectText {
  font-size: 12px;
  color: #999;
}

.circleText{
  font-size: 10px;
  font-weight: bolder;
}

.introduction {
  font-size: 15px;
  font-family: sans-serif;
  color: #333;
  padding: 0 20px 0 20px;
}

.d3-component{
  height: 100%!important;
  width: 100%!important;
  margin: 0px!important;
  margin-top: 2px;
  padding: 0px;
  background-color: white;
  transform-origin: 0, 0;
  overflow-y: visible!important;
}

.shadow{
  box-shadow: 2px 2px 10px 1px rgba(0, 0, 0, 0.25);
}

.rounded, .shadow, .shadow >* {
  border-radius: 1em!important;
}

button {
  border: none;
  color: black;
  /* padding: 5px 5px; */
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 1em;
  border-radius: 8px;
  cursor:pointer;
  height: 100%;
  min-height: 1em;
  max-height: 2em;
}

.activeButton{
  background-color: white; /* Green */
  border: 2px solid #4CAF50;
}

.inactiveButton{
  background-color:#4CAF50;
}